@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
html {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: "Raleway";
}

span {
  display: inline-block;
}

a {
  text-decoration: none;
}

#title_line {
  width: 150px;
  height: 5px;
  margin: 0 auto;
  margin-top: 5px;
  background-color: #D2AA68;
}

.header {
  width: 100%;
  height: 10vh;
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  z-index: 15;
  background-color: rgba(0, 0, 0, 0.4);
}
@media (max-width: 680px) {
  .header {
    height: 15vh;
    align-items: flex-start;
  }
}
.header__logo {
  margin-left: 5%;
  height: 10vh;
  display: flex;
  align-items: center;
}
.header__logo_icon {
  height: 70%;
}
.header .navbar {
  height: 100%;
  width: 35%;
  margin-left: 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 840px) {
  .header .navbar {
    width: 40%;
  }
}
@media (max-width: 680px) {
  .header .navbar {
    width: 70%;
    height: 50%;
  }
}
.header .navbar__links {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
@media (max-width: 680px) {
  .header .navbar__links {
    height: 50%;
  }
}
.header .navbar__links_title {
  font-size: 24px;
  font-weight: 400;
  color: #ffffff;
  transition-property: all;
  transition: linear 0.1s;
}
.header .navbar__links_title:hover {
  color: #D2AA68;
}
@media (max-width: 680px) {
  .header .navbar__links_title {
    font-size: 20px;
  }
}
.header .phone, .header .email {
  position: absolute;
  right: 5%;
  height: 100%;
  width: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 840px) {
  .header .phone, .header .email {
    right: 0%;
    width: 40%;
  }
}
@media (max-width: 680px) {
  .header .phone, .header .email {
    width: 65%;
    height: 50%;
    align-self: flex-end;
  }
}
.header .phone__link, .header .email__link {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.header .phone__link_title, .header .email__link_title {
  cursor: pointer;
  font-size: 32px;
  font-weight: 700;
  font-family: "Roboto";
  color: #D2AA68;
  transition: linear 0.1s;
}
.header .phone__link_title:hover, .header .email__link_title:hover {
  color: #ffffff;
}
@media (max-width: 680px) {
  .header .phone__link_title, .header .email__link_title {
    font-size: 24px;
  }
}

.greeting {
  position: relative;
  flex-wrap: wrap;
  height: 120vh;
  color: #ffffff;
  background: url("../images/banner.png");
