  background-position: center center;
  padding-top: 10vh;
}
@media (max-width: 840px) {
  .greeting {
    height: 110vh;
  }
}
@media (max-width: 680px) {
  .greeting {
    display: block;
    padding-top: 10vh;
  }
}
@media (max-height: 700px) {
  .greeting {
    height: 150vh;
  }
}
@media (max-height: 600px) {
  .greeting {
    height: 170vh;
  }
}
@media (max-height: 500px) {
  .greeting {
    height: 200vh;
  }
}
@media (max-width: 540px) {
  .greeting {
    padding-top: 20vh;
  }
}
.greeting-section__text {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 30%;
  width: 60%;
  margin-top: 10%;
  margin-left: 10%;
}
@media (max-width: 1440px) {
  .greeting-section__text {
    margin-top: 5%;
    height: 40%;
  }
}
@media (max-width: 840px) {
  .greeting-section__text {
    height: 30%;
  }
}
@media (max-width: 680px) {
  .greeting-section__text {
    width: 70%;
    height: 20%;
    margin-top: 20%;
  }
}
@media (max-width: 540px) {
  .greeting-section__text {
    width: 90%;
    height: 20%;
  }
}
@media (max-width: 480px) {
  .greeting-section__text {
    margin-left: 5%;
  }
}
.greeting-section__text_title {
  font-weight: 700;
  font-size: 70px;
}
@media (max-width: 1020px) {
  .greeting-section__text_title {
    font-size: 60px;
  }
}
@media (max-width: 840px) {
  .greeting-section__text_title {
    font-size: 50px;
  }
}
@media (max-width: 480px) {
  .greeting-section__text_title {
    font-size: 40px;
  }
}
.greeting-section__text_description {
  font-size: 24px;
  line-height: 35px;
}
@media (max-width: 840px) {
  .greeting-section__text_description {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (max-width: 480px) {
  .greeting-section__text_description {
    font-size: 40px;
  }
}
.greeting-section__images {
  position: relative;
  left: 10%;
  z-index: 10;
  width: 80%;
}
@media (max-width: 680px) {
  .greeting-section__images {
    margin-top: 15vh;
    width: 70%;
  }
}
@media (max-width: 480px) {
  .greeting-section__images {
    left: 5%;
  }
}
.greeting-section__slider {
  position: relative;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}
@media (max-width: 680px) {
  .greeting-section__slider {
    margin: 0 auto;
  }
}
.greeting-section__slider .slides-nav_m {
  position: absolute;
  bottom: 0px;
  display: flex;
  justify-content: center;
}
.greeting-section__slider .slides-nav_m .m-btn {
  padding: 8px;
  border-radius: 10px;
  cursor: pointer;
  transition: 1s;
}
.greeting-section__slider .slides-nav_m .m-btn:not(:last-child) {
  margin-right: 30px;
}
.greeting-section__slider .slides-nav_m .m-btn:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.greeting-section__slider #radio1:checked ~ .first_image {
  margin-left: 0;
}
.greeting-section__slider #radio2:checked ~ .first_image {
  margin-left: -25%;
}
.greeting-section__slider #radio3:checked ~ .first_image {
  margin-left: -50%;
}
.greeting-section__slider #radio4:checked ~ .first_image {
  margin-left: -75%;
}
.greeting-section__slides {
  height: 300px;
  width: 400%;
  display: flex;
}
@media (max-width: 680px) {
  .greeting-section__slides {
    height: 400px;
  }
}
@media (max-height: 500px) {
  .greeting-section__slides {
    height: 300px;
  }
}
.greeting-section__slides input {
  display: none;
}
.greeting-section__slides .slide__image {
  width: 25%;
  transition: 1s;
}
.greeting-section__slides .slide__image_content {
  display: flex;
  flex-direction: column;
}
.greeting-section__slides .slide__image_content span {
  display: inline-block;
  font-weight: 500;
  font-size: 24px;
}
@media (max-width: 680px) {
  .greeting-section__slides .slide__image_content span {
    font-size: 20px;
  }
}
.greeting-section__slides .slides-nav__auto {
  position: absolute;
  bottom: 0px;
  display: flex;
  justify-content: center;
}
.greeting-section__slides .slides-nav__auto div {
  background-color: #D2AA68;
  padding: 8px;
  border-radius: 10px;
  transition: 1s;
}
.greeting-section__slides .slides-nav__auto div:not(:last-child) {
  margin-right: 30px;
}
.greeting-section__slides #radio1:checked ~ .slides-nav__auto > .slides-nav__auto_b1 {
  background-color: rgba(255, 255, 255, 0.5);
}
.greeting-section__slides #radio2:checked ~ .slides-nav__auto .slides-nav__auto_b2 {
  background-color: rgba(255, 255, 255, 0.5);
}
.greeting-section__slides #radio3:checked ~ .slides-nav__auto .slides-nav__auto_b3 {
  background-color: rgba(255, 255, 255, 0.5);
}
.greeting-section__slides #radio4:checked ~ .slides-nav__auto .slides-nav__auto_b4 {
  background-color: rgba(255, 255, 255, 0.5);
}
.greeting-button_more {
  position: relative;
  z-index: 10;
  top: -20%;
  left: 10%;
