    height: 65px;
    width: 480px;
    top: -15%;
  }
}
@media (max-width: 680px) {
  .greeting-button_more {
    width: 400px;
  }
}
@media (max-width: 540px) {
  .greeting-button_more {
    width: 320px;
  }
}
@media (max-width: 480px) {
  .greeting-button_more {
    width: 320px;
    left: 5%;
  }
}
@media (max-height: 500px) {
  .greeting-button_more {
    top: -20%;
  }
}
@media (max-width: 340px) {
  .greeting-button_more {
    width: 290px;
  }
}
.greeting-button_more:hover {
  background-color: #ffffff;
}
.greeting-button_more_text {
  width: 100%;
  height: 100%;
  display: inline-block;
  font-weight: 700;
