.advantages {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000000;
}
.advantages-section__text {
  width: 80%;
  height: 15vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.advantages-section__text_title {
  height: 15vh;
  line-height: 15vh;
  font-weight: 700;
  font-size: 70px;
}
@media (max-width: 1020px) {
  .advantages-section__text_title {
    font-size: 60px;
  }
}
@media (max-width: 840px) {
  .advantages-section__text_title {
    font-size: 50px;
  }
}
@media (max-width: 480px) {
  .advantages-section__text_title {
    font-size: 40px;
  }
}
.advantages-section__list {
  margin-top: 5vh;
  margin-bottom: 15vh;
  width: 90%;
}
@media (max-width: 1020px) {
  .advantages-section__list {
    width: 100%;
  }
}
.advantages-section__item {
  cursor: pointer;
  width: 90%;
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #D2AA68;
  transition: 0.1s linear;
}
.advantages-section__item:hover {
  background-color: rgba(210, 170, 104, 0.45);
  border-bottom: 2px solid transparent;
}
@media (max-width: 840px) {
  .advantages-section__item {
    height: 80px;
  }
}
@media (max-width: 680px) {
  .advantages-section__item {
    height: 100px;
  }
}
.advantages-section__item_title {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.advantages-section__item_title_text {
  width: 85%;
  display: inline-block;
  text-align: start;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
}
@media (max-width: 840px) {
  .advantages-section__item_title_text {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (max-width: 540px) {
  .advantages-section__item_title_text {
    font-size: 16px;
    line-height: 20px;
  }
}
.advantages-section__item_plus {
  height: 50px;
  width: 50px;
  font-size: 15px;
  opacity: 0.7;
}
.advantages-section__item_plus .circle {
  position: relative;
  width: 2.55em;
  height: 2.5em;
}
.advantages-section__item_plus .circle .horizontal {
  position: absolute;
  background-color: #000000;
  width: 30px;
  height: 5px;
  left: 50%;
  margin-left: -15px;
  top: 50%;
  margin-top: -2.5px;
}
.advantages-section__item_plus .circle .vertical {
  position: absolute;
  background-color: #000000;
  width: 5px;
  height: 30px;
  left: 50%;
  margin-left: -2.5px;
  top: 50%;
  margin-top: -15px;
}
.advantages-section__item .opened {
  opacity: 1;
}
.advantages-section__item .opened .vertical {
  transition: all 0.5s ease-in-out;
  transform: rotate(90deg);
}
.advantages-section__item .opened .horizontal {
  transition: all 0.5s ease-in-out;
  transform: rotate(90deg);
  opacity: 0;
}
.advantages .section__item_open {
  background-color: #D2AA68;
}
.advantages .section__item_open:hover {
  background-color: rgba(210, 170, 104, 0.45);
}
.advantages-section__subtitle {
  overflow: hidden;
  margin-left: 5%;
  width: 90%;
  max-height: 250px;
  padding-top: 35px;
  padding-bottom: 40px;
  background-color: #D2AA68;
  transition: all 1s;
}
.advantages-section__subtitle ul {
  list-style: none;
}
@media (max-width: 840px) {
  .advantages-section__subtitle ul {
    padding-inline-start: 20px;
  }
}
@media (max-width: 680px) {
  .advantages-section__subtitle ul {
    padding-inline-start: 10px;
  }
}
@media (max-width: 540px) {
  .advantages-section__subtitle ul {
    padding-inline-start: 0px;
  }
}
.advantages-section__subtitle ul li {
  padding-left: 20px;
  display: flex;
  align-items: flex-start;
}
.advantages-section__subtitle ul li::before {
  content: "-";
  display: inline-block;
  margin-right: 20px;
  line-height: 30px;
}
@media (max-width: 840px) {
  .advantages-section__subtitle ul li::before {
    line-height: 20px;
  }
}
.advantages-section__subtitle_text {
  font-size: 20px;
  line-height: 30px;
}
@media (max-width: 840px) {
  .advantages-section__subtitle_text {
    font-size: 16px;
    line-height: 20px;
  }
}
.advantages .section__subtitle_close {
  max-height: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
}

